@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

@layer base {
    @font-face {
        font-family: Perfect Darks;
        font-weight: 400;
        src: url(./fonts/PerfectDarks/pdark.ttf) format("woff");
    }
}

.panel {
    background-image: linear-gradient(to bottom, rgba(108, 38, 36, .9), rgba(0, 0, 0, .9)), url(./Assets/Images/dust_2_bg.jpg);
}

.panel-stats {
    background-image: linear-gradient(to bottom, rgba(54, 69, 79, .9), rgba(73, 93, 106, 0.9)), url(./Assets/Images/inferno_bg.jpg);
}

.fa-instagram {
    color: white;
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285aeb 90%);
    background: -webkit-radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285aeb 90%);
    background-clip: text;
}